{/* =====タブレット版 =====*/}
@media (max-width: 1000px) {
  .header-wrapper{
    z-index: 0;
    position:static;
    height: 20px;
  }
  .main-container{
    margin-right: 5px;
    margin-left: 5px;
  }
  .top-container{
    min-height: 50px;
    padding-top: 30px;
    padding-bottom:20px;
  }

  .top{
    font-size: 40px;
  }
  .message-container{
  max-width:700px;
  margin: 0 auto;
  margin-bottom:30px;
  }
  .message-sheet{
    max-width:700px;
    padding: 10px 30px;
    background-color: #F5B1A4;
    border-radius: 3px;
  }

      .message-frame-top{
        height: 120px;
        background-image: url('https://raw.githubusercontent.com/haruka71/materials/master/message-frame.png');
        background-repeat: no-repeat;
        background-position: 50% 0%;
        background-attachment: local;
        background-size: auto 500px;
        margin-bottom:10px;
      }

      .message-box{
        max-width: 700px;
        margin: 0 auto;
        padding: 30px 30px;
        background-color: #FEFDFB;
        border-radius: 15px;
      }

        .message-main{
          text-align: center;
          font-size: 36px;
          line-height: 32px;
          margin-top: 10px;
        }

        .message-to{
          font-size: 18px;
          margin-left: 15px;
        }

        .message-text{
          text-align: center;
          font-size: 18px;
        }

        .message-from{
          font-size: 18px;
          text-align: right;
          margin-right: 15px;
        }

      .message-frame-bottom{
        height: 120px;
        background-image: url('https://raw.githubusercontent.com/haruka71/materials/master/message-frame.png');
        background-repeat: no-repeat;
        background-position: 50% 0%;
        background-attachment: local;
        background-size: auto 500px;
        margin-top:10px;
      }


{/* 説明文 */}
.explanation-wrapper{
display: flex;
align-items: center;
margin-bottom:30px;
}

.explanation-container{
  width:900px;
  margin: 0 auto;
  padding:0px 50px;
  border-top: double 5px #F5B1A4;
  border-bottom: double 5px #F5B1A4;
}

  .explanation-title{
    text-align: center;
    font-size: 18px;
  }

  .explanation-text{
    text-align: center;
    font-size: 14px;
  }

{/* カタログ */}

.catalog-container{
  max-width:700px;
  padding-top:10px;
  padding-bottom: 40px;
  padding-left: 30px;
  padding-right: 30px;
  border-radius: 3px;
  margin-bottom: 30px;
}

  .catalog-title{
    font-size: 40px;
    margin-bottom:20px;
  }

  .gift-container {
    min-height: 150px;
    max-width: 700px;
    padding: 25px 40px;
    margin-bottom:30px;
    border-radius: 15px;
  }

      .gift-title{
        font-size: 20px;
        font-weight: bold;
        margin-top: 5px;
        margin-bottom: 10px;
        margin-left: 20px;
        margin-right:20px;
        min-height: 0px;
      }

      .gift-intro-box{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-height: 200px;
        margin: 0px 0px;
      }

        .gift-image-box{
          display: flex;
          justify-content: center;
          max-height: 200px;
          min-height: 0px;
          width: 100%;
          padding: 10px 0px;
        }
          .gift-image{
            max-height:180px;
            margin: 0px 0px;
          }

        .gift-text-box{
          display: flex;
          flex-direction: column;
          align-items: center;
          margin-left:0px;
          width: 100%;
          max-height:100%;
          float: none;
          }

          .gift-message-box{
            display: flex;
            justify-content: center;
            align-items: center;
            width: 100%;
            min-height: 100px;
            margin-bottom: 20px;
          }

            .gift-message{
              font-size: 14px;
              padding-top: 10px;
              padding-bottom: 10px;
            }

            .gift-link{
              font-size: 14px;
            }


          .gift-button-box{
            width:100%;
            display: flex;
            justify-content: center;
          }
            .gift-button{
              font-size: 18px;
              border-radius: 7px;
              padding: 7px 10px;
              width:80%;
            }

{/* ▼▼フッター▼▼ */}
.footer-wrapper{
height: 25px;
}



