{/* =====PC版===== */}
* {
  box-sizing: border-box;
}

{/* ▼▼ヘッダー▼▼ */}
.header-wrapper{
  z-index: 10;
  position: fixed;
  top: 0;
  background-color: #C3A8EF;
  width: 100%;
  height: 50px;
}

{/* ▼▼メイン▼▼ */}
body{
  font-family:
    'Avenir','Helvetica Neue','Helvetica','Arial','Hiragino Sans','ヒラギノ角ゴシック',YuGothic,'Yu Gothic','メイリオ', Meiryo,'ＭＳ Ｐゴシック','MS PGothic';
  color: #403D3D;
  background-color: #FEFDFB;
}

.main-container{
  margin-right: 15px;
  margin-left: 15px;
}

{/* トップメッセージ */}
.top-wrapper{

}

  .top-container{
    max-width:1500px;
    margin: 0 auto;
    min-height: 200px;
    padding-top: 150px;
    padding-bottom:100px;
  }

  .top{
    text-align: center;
    font-size: 80px;
    text-decoration:underline;
    text-decoration-color:#F5B1A4;
  }

{/* メッセージ */}

.message-wrapper{
  max-width:1000px;
}

  .message-container{
  max-width:1000px;
  margin: 0 auto;
  margin-bottom:50px;
  }

    .message-sheet{
      max-width:1000px;
      padding: 40px 60px;
      background-color: #F5B1A4;
      border-radius: 5px;
    }

        .message-frame-top{
          height: 150px;
          background-image: url('https://raw.githubusercontent.com/haruka71/materials/master/message-frame.png');
          background-repeat: no-repeat;
          background-position: 50% 0%;
          background-attachment: local;
          margin-bottom:20px;
        }

        .message-box{
          max-width: 800px;
          margin: 0 auto;
          padding: 70px 70px;
          background-color: #FEFDFB;
          border-radius: 20px;
        }

          .message-main{
            text-align: center;
            font-size: 70px;
            line-height: 80px;
            margin-top: 20px;
          }

          .message-to{
            font-size: 24px;
            margin-left: 30px;
          }

          .message-text{
            text-align: center;
            font-size: 24px;
          }

          .message-from{
            font-size: 24px;
            text-align: right;
            margin-right: 30px;
          }

        .message-frame-bottom{
          height: 150px;
          background-image: url('https://raw.githubusercontent.com/haruka71/materials/master/message-frame.png');
          background-repeat: no-repeat;
          background-position: 50% 0%;
          background-attachment: local;
          margin-top:20px;
        }


{/* 説明文 */}
.explanation-wrapper{
  display: flex;
  align-items: center;
  margin-bottom:50px;
}

  .explanation-container{
    width:900px;
    margin: 0 auto;
    padding:0px 50px;
    border-top: double 5px #F5B1A4;
    border-bottom: double 5px #F5B1A4;
  }

    .explanation-title{
      text-align: center;
      font-size: 30px;
      font-weight: bold;
    }

    .explanation-text{
      text-align: center;
      font-size: 20px;
    }

{/* カタログ */}
.catalog-wrapper{
}

  .catalog-container{
    max-width:1350px;
    margin: 0 auto;
    padding-top:20px;
    padding-bottom: 40px;
    padding-left: 50px;
    padding-right: 50px;
    background-color: #C3A8EF;
    border-radius: 5px;
    margin-bottom: 50px;
  }

    .catalog-title{
      text-align: center;
      font-size: 70px;
      margin-bottom:70px;
    }

    .gift-list {
      display: flex;
      flex-direction: column;
      align-items: center;

    }

    .gift-container {
      min-height: 350px;
      max-width: 1100px;
      margin: 0 auto;
      padding: 40px;
      margin-bottom:50px;
      background-color: #FEFDFB;
      border-radius: 20px;
    }

        .gift-title{
          text-align: center;
          font-size: 30px;
          font-weight: bold;
          margin-top: 15px;
          margin-bottom: 10px;
          margin-left: 50px;
          margin-right:50px;
          min-height: 35px;
        }

        .gift-intro-box{
          display: flex;
          min-height: 315px;
          margin: 0px 50px;
        }

          .gift-image-box{
            width: 40%;
            min-height: 315px;
          }
            .gift-image{
              max-width: 100%;
              max-height: 300px;
              margin: 30px 0px;
            }

          .gift-text-box{
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-left:50px;
            width: 60%;
            height:100%;
            float: right;
            }

            .gift-message-box{
              display: flex;
              justify-content: center;
              align-items: center;
              width: 100%;
              min-height: 230px;
              margin-bottom: 20px;
            }

            .gift-message-box2{
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 100%;
            }

              .gift-message{
                font-size: 20px;
                padding-top: 30px;
                padding-bottom: 20px;
                text-align: center;
              }

              .gift-link{
                color: #403D3D;
                font-size: 20px;
                text-align: center;
              }

              .gift-link:link{
                color: #403D3D;
              }
              .gift-link:visited{
                color: #403D3D;
              }

            .gift-button-box{
            }
              .gift-button{
                font-size: 20px;
                background-color: #F5B1A4;
                border-radius: 7px;
                padding: 10px 20px;
                font-weight: bold;
                cursor: pointer;
                border-style: none;
              }
              .gift-button:hover {
                background-color: #F59F8E;
              }
              .clear{
                clear:left;
              }
{/* ▼▼モーダル▼▼ */}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.6);
}

.modal-inner {
  position: absolute;
  top: 5%;
  right: 0;
  left: 0;
  width: 90%;
  padding-bottom: 10px;
  margin: auto;
  background-color: #FEFDFB;
  border-radius: 7px;
}

.modal-header {
  height: 15px;
  width: 100%;
  border-radius: 7px 7px 0 0;
  background-color: #C3A8EF;
}

.modal-main{
  padding-top:0px;
  padding-bottom:10px;
  padding-right:20px;
  padding-left:20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mordal-top{
  font-size: 24px;
  background-color: #F5B1A4;
  border-radius: 3px;
  padding: 10px 20px;
  font-weight: bold;
  cursor: pointer;
  border-style: none;
  margin-bottom: 10px;
}

.modal-gift{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.modal-title{
  font-size: 18px;
  font-weight:bold;
  margin: 10px 10px;
  text-align:center;
}

.modal-image{
  max-width: 100%;
  max-height: 180px;
  margin: 10px 0px;
}

.modal-explanation{
  font-size: 12px;
  text-align: center;
  margin: 10px 20px
}

.modal-close-btn {
  font-size: 14px;
  padding: 5px 10px;
  border: 0;
  background-color: #f0f4f9;
  cursor: pointer;
  margin-top: 5px;
}

.modal-close-btn:hover {
  color: #8491a5;
  background-color: #ccd9ea;
  transition: .3s ease-in-out;
  border-radius: 3px;
}

{/* ▼▼フッター▼▼ */}
.footer-wrapper{
  z-index: 10;
  background-color: #F5B1A4;
  height: 50px;
}


{/* =====PC版終わり===== */}






